import axios from "axios";
import React, { memo, useEffect, useState } from "react";
import Chart from "./Chart";
import Menu from "./Menu";
import Option from "./Option";
import Title from "./Title";
import { isMobile, MobileView } from "react-device-detect";
import TradingViewWidget, { Themes } from "react-tradingview-widget";

const Main = (props) => {
  const [listSymbol, setListSymbol] = useState(() => []);
  const [data, setData] = useState(() => []);
  const [symbol, setSymBol] = useState(() => "BTC");
  const [timeType, setTimeType] = useState(() => ({
    time: 5,
    timeType: 3,
    isHour: false,
  }));
  useEffect(() => {
    (async () => {
      const res = await axios({
        url: `https://fapi.coinglass.com/api/support/symbol`,
        method: "get",
        responseType: "json",
      });
      const result = await res.data;
      setListSymbol(() => result.data);
    })();
    return () => setListSymbol(() => []);
  }, []);
  useEffect(() => {
    (async () => {
      const res = await axios({
        url: `https://fapi.coinglass.com/api/futures/longShortRate?symbol=${symbol}&timeType=${parseInt(
          timeType.timeType
        )}`,
        method: "get",
        responseType: "json",
      });
      const result = await res.data;
      setData(() => result.data[0]);
    })();
  }, [timeType, symbol]);
  useEffect(() => {
    const intervalId = setInterval(async () => {
      const res = await axios({
        url: `https://fapi.coinglass.com/api/futures/longShortRate?symbol=${symbol}&timeType=${parseInt(
          timeType.timeType
        )}`,
        method: "get",
        responseType: "json",
      });
      const result = await res.data;
      setData(() => result.data[0]);
    }, 3000);

    return () => clearInterval(intervalId);
  }, [data, timeType, symbol]);
  const [select, setSelect] = useState(() => 1);
  return (
    <>
      <div
        className="fjirfjgorjeiorj"
        style={{
          width: "100%",
          minHeight: "100vh",
          backgroundColor: "#17171a",
        }}
      >
        {isMobile === true && (
          <MobileView>
            <div style={{ width: "100%", height: "50vh" }}></div>
          </MobileView>
        )}

        {select === 1 && (
          <>
            <div style={{position: "relative", zIndex: 20, background: "#17171a", minHeight: "100vh"}}>
              {isMobile === true && (
                <MobileView>
                  <div style={{ height: 141, width: "100%" }}></div>
                </MobileView>
              )}
              <div style={{ paddding: 24 }}>
                <Title {...data}></Title>
                <Option
                  listSymbol={listSymbol}
                  symbol={symbol}
                  timeType={timeType}
                  setSymBol={setSymBol}
                  setTimeType={setTimeType}
                  {...data}
                ></Option>
                <br />
                <Chart {...data}></Chart>
              </div>
            </div>
          </>
        )}
        {select === 3 && <Trading symbol={"ETH"}></Trading>}
        {select === 4 && <Trading symbol={"FOREXCOM:NSXUSD"}></Trading>}
        {select === 2 && <></>}
      </div>
      <Menu setSelect={setSelect} select={select}></Menu>
      {isMobile === true && (
        <MobileView>
          <div style={{ width: "100%", height: "50px" }}></div>
        </MobileView>
      )}
    </>
  );
};

export default Main;

const Trading = memo((props) => {
  return (
    <>
      <TradingViewWidget
        symbol={props?.symbol}
        theme={Themes.DARK}
        locale={"vi_VN"}
        width={"100%"}
        className="fjioedjpoijfedpiojr"
        timezone="Asia/Ho_Chi_Minh"
        toolbar_bg={"#f1f3f6"}
        enable_publishing={false}
        allow_symbol_change={true}
        interval={1}
      />
    </>
  );
});
